.header {
  max-width: 880px;
  margin: 0 auto;
  padding-top: 45px;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
}

@media screen and (max-width: 950px) {
  .header {
    max-width: 90%;
  }
}

@media screen and (max-width: 600px) {
  .header {
    max-width: 100%;
    padding-top: 28px;
  }
}